<template>
	<view class="body">
		<!-- 步骤条组件 -->
		<u-steps :current="steps" activeColor="#ffd656" inactiveColor="#cfcfcf" :dot="true">
			<u-steps-item></u-steps-item>
			<u-steps-item></u-steps-item>
			<u-steps-item></u-steps-item>
		</u-steps>
		<!-- 步骤条文字 -->
		<view class="textList">
			<view :class="{checked:steps==0 || steps==1 || steps==2}">
				<view class="text">Step1</view>
				<view class="text">确认信息</view>
			</view>
			<view :class="{checked:steps==1 || steps==2}">
				<view class="text">Step2</view>
				<view class="text">估分</view>
			</view>
			<view :class="{checked:steps==2}">
				<view class="text">Step3</view>
				<view class="text">查看报告</view>
			</view>
		</view>
		<view class="content">
			<view class="list">
				<view :class="{cell:true, disable:city==0}">
					<view class="label">省市:</view>
					<view class="check">
						<uni-data-select v-model="city" :localdata="cityRange" @change="changeCity"></uni-data-select>
					</view>
				</view>
				<view :class="{cell:true, disable:company==0}">
					<view class="label">招生单位:</view>
					<view class="check">
						<uni-data-select v-model="company" :localdata="companyRange" @change="changeCompany">
						</uni-data-select>
					</view>
				</view>
				<view class="cell">
					<view class="label">学硕/专硕:</view>
					<view class="check">
						<uni-data-select v-model="majorType" :localdata="majorTypeRange" @change="changemajorType"></uni-data-select>
					</view>
				</view>
				<view :class="{cell:true, disable:type==0}">
					<view class="label">学科门类:</view>
					<view class="check">
						<uni-data-select v-model="type" :localdata="typeRange" @change="changeType"></uni-data-select>
					</view>
				</view>
				<view :class="{cell:true, disable:subject==0}">
					<view class="label">一级学科:</view>
					<view class="check">
						<uni-data-select v-model="subject" :localdata="subjectRange" @change="changeSubject">
						</uni-data-select>
					</view>
				</view>
				<view :class="{cell:true, disable:major==0}">
					<view class="label">专业名称:</view>
					<view class="check">
						<uni-data-select v-model="major" :localdata="majorRange" @change="changeMajor">
						</uni-data-select>
					</view>
				</view>
			</view>
			<view class="title">考试科目</view>
			<view class="testMoudel">
				<view class="part">
					<view class="cell">
						<view class="subNum">第一门</view>
						<view :class="{check:true, defult:subOne==0}">
							<uni-data-select v-model="subOne" :localdata="subOneRange" @change="changeOne"></uni-data-select>
						</view>
					</view>
					<view class="cell">
						<view class="subNum">第二门</view>
						<view :class="{check:true, defult:subTwo==0}">
							<uni-data-select v-model="subTwo" :localdata="subTwoRange" @change="changeTwo"></uni-data-select>
						</view>
					</view>
				</view>
				<view class="part">
					<view class="cell">
						<view class="subNum">第三门</view>
						<view :class="{check:true, defult:subThree==0}">
							<uni-data-select v-model="subThree" :localdata="subThreeRange" @change="changeThree"></uni-data-select>
						</view>
					</view>
					<view class="cell">
						<view class="subNum">第四门</view>
						<view :class="{check:true, defult:subFour==0}">
							<uni-data-select v-model="subFour" :localdata="subFourRange" @change="changeFour"></uni-data-select>
						</view>
					</view>
				</view>
			</view>
			<button class="beginButton" @click="toNext">开始估分</button>
		</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import {
		area, school, major, nextMajor, subject, submitInfo, assessment
	} from '@/common/http.api.js';
	export default {
		data() {
			return {
				token:'',
				steps: 0, //当前步骤
				city: 0, //当前城市
				company: 0, //当前单位
				majorType:0, //学硕/专硕
				type: 0, //当前学科门类
				subject: 0, //当前一级学科
				major: 0, //当前专业名称
				cityRange: [{
						value: 0,
						text: '--省市名称--'
					},
				],
				companyRange: [{
						value: 0,
						text: '--单位名称--'
					},
				],
				majorTypeRange: [{
						value: 0,
						text: '学硕'
					},
					{
						value: 1,
						text: '专硕'
					},
				],
				typeRange: [{
						value: 0,
						text: '--学科门类名称--'
					},
				],
				subjectRange: [{
						value: 0,
						text: '--学科名称--'
					},
				],
				majorRange: [{
						value: 0,
						text: '--专业名称--'
					},
				],
				subOne: 0, //当前第一门
				subOneRange: [{
						value: 0,
						text: '请选择政治'
					}
				],
				subTwo: 0, //当前第二门
				subTwoRange: [{
						value: 0,
						text: '请选择外语'
					}
				],
				subThree: 0, //当前第三门
				subThreeRange: [{
						value: 0,
						text: '请选择数学'
					}
				],
				subFour: 0, //当前第四门
				subFourRange: [{
						value: 0,
						text: '请选择专业课'
					}
				],
				area_id:'',
				school_id:'',
				one_major_id:'',
				two_major_id:'',
				three_major_id:'',
				one_subject:'',
				two_subject:'',
				three_subject:'',
				four_subject:'',
				data:{}
			}
		},
		onShareAppMessage() {
			//分享
			wx.showShareMenu({
		      withShareTicket: true,
		      menus: ['shareAppMessage', 'shareTimeline']
		     })
		    return {
			  title: '考研调剂平台小程序',
		      path: '/pages/assessment/assessment',
			  imageUrl:'/static/assessment/share.png', //设置分享图片
		      // promise 
		    }
		},
		onShareTimeline: function () {
			//分享监听事件
			return {
			  title: '	考研调剂平台小程序',
			}
		},
		onLoad() {
			this.token = store.state.vuex_token
			area().then(res=>{
				console.log(res)
				res.data.data.map((item,index)=>{
					let arr = {
						value: index+1,
						text: item.name,
						id:item.id
					}
					this.cityRange.push(arr)
				})
			}).catch(err=>{
				console.log(err)
			})
			major({major_type:'academic_master'}).then(res=>{
				console.log(res)
				res.data.data.map((item,index)=>{
					let arr = {
						value: index+1,
						text: item.name,
						id:item.id
					}
					this.typeRange.push(arr)
				})
			}).catch(err=>{
				console.log(err)
			})
			subject({type:'one_subject'}).then(res=>{
				console.log(res)
				res.data.data.map((item,index)=>{
					let arr = {
						value: index+1,
						text: item.title,
						id:item.id
					}
					this.subOneRange.push(arr)
				})
			}).catch(err=>{
				console.log(err)
			})
			subject({type:'two_subject'}).then(res=>{
				console.log(res)
				res.data.data.map((item,index)=>{
					let arr = {
						value: index+1,
						text: item.title,
						id:item.id
					}
					this.subTwoRange.push(arr)
				})
			}).catch(err=>{
				console.log(err)
			})
			subject({type:'three_subject'}).then(res=>{
				console.log(res)
				res.data.data.map((item,index)=>{
					let arr = {
						value: index+1,
						text: item.title,
						id:item.id
					}
					this.subThreeRange.push(arr)
				})
			}).catch(err=>{
				console.log(err)
			})
			subject({type:'four_subject'}).then(res=>{
				console.log(res)
				res.data.data.map((item,index)=>{
					let arr = {
						value: index+1,
						text: item.title,
						id:item.id
					}
					this.subFourRange.push(arr)
				})
			}).catch(err=>{
				console.log(err)
			})
			assessment({token:this.token}).then(res=>{
				console.log(res)
				this.data = res.data.data
				if(res.data.data.state==1){
					uni.redirectTo({
						url:'/pages/assessment/report'
					})
				}else if(this.data){
					uni.redirectTo({
						url:'/pages/assessment/assessmentStepTwo'
					})
				}
			}).catch(err=>{
				console.log(err)
			})
		},
		onShow() {
			
		},
		methods: {
			//选择城市
			changeCity(e) {
				console.log("当前选择:", e)
				this.companyRange.splice(1,this.companyRange.length-1)
				this.area_id = this.cityRange[e].id
				school({area:this.cityRange[e].text}).then(res=>{
					console.log(res)
					res.data.data.map((item,index)=>{
						let arr = {
							value: index+1,
							text: item.name,
							id:item.id
						}
						this.companyRange.push(arr)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			//选择单位
			changeCompany(e) {
				console.log("当前选择:", e)
				this.school_id = this.companyRange[e].id
			},
			//选择学硕/专硕
			changemajorType(e) {
				console.log("当前选择:", e)
				this.typeRange.splice(1,this.typeRange.length-1)
				if(e==0){
					major({major_type:'academic_master'}).then(res=>{
						console.log(res)
						res.data.data.map((item,index)=>{
							let arr = {
								value: index+1,
								text: item.name,
								id:item.id
							}
							this.typeRange.push(arr)
						})
					}).catch(err=>{
						console.log(err)
					})
				}else{
					major({major_type:'pro_master'}).then(res=>{
						console.log(res)
						res.data.data.map((item,index)=>{
							let arr = {
								value: index+1,
								text: item.name,
								id:item.id
							}
							this.typeRange.push(arr)
						})
					}).catch(err=>{
						console.log(err)
					})
				}
				
			},
			//选择门类
			changeType(e) {
				console.log("当前选择:", e)
				this.one_major_id = this.typeRange[e].id
				this.subjectRange.splice(1,this.subjectRange.length-1)
				nextMajor({id:this.typeRange[e].id}).then(res=>{
					console.log(res)
					res.data.data.map((item,index)=>{
						let arr = {
							value: index+1,
							text: item.name,
							id:item.id
						}
						this.subjectRange.push(arr)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			//选择学科
			changeSubject(e) {
				console.log("当前选择:", e)
				this.two_major_id = this.subjectRange[e].id
				this.majorRange.splice(1,this.majorRange.length-1)
				nextMajor({id:this.subjectRange[e].id}).then(res=>{
					console.log(res)
					res.data.data.map((item,index)=>{
						let arr = {
							value: index+1,
							text: item.name,
							id:item.id
						}
						this.majorRange.push(arr)
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			//选择专业
			changeMajor(e) {
				console.log("当前选择:", e)
				this.three_major_id = this.majorRange[e].id
			},
			// 选择第一门
			changeOne(e) {
				this.one_subject = this.subOneRange[e].id
			},
			// 选择第二门
			changeTwo(e) {
				this.two_subject = this.subTwoRange[e].id
			},
			// 选择第三门
			changeThree(e) {
				this.three_subject = this.subThreeRange[e].id
			},
			// 选择第四门
			changeFour(e) {
				this.four_subject = this.subFourRange[e].id
			},
			// 去下一步
			toNext() {
				if(this.data){
					uni.navigateTo({
						url:'/pages/assessment/assessmentStepTwo'
					})
				}else{
					// 组装提交信息
					let params = {
						token:this.token,
						area_id:this.area_id,
						school_id:this.school_id,
						one_major_id:this.one_major_id,
						two_major_id:this.two_major_id,
						three_major_id:this.three_major_id,
						one_subject:this.one_subject,
						two_subject:this.two_subject,
						three_subject:this.three_subject,
						four_subject:this.four_subject,
					}
					// 组装提示信息
					let tipArr = [
						{datas:this.area_id,text:'当前选择省市为空'},
						{datas:this.school_id,text:'当前选择招生单位为空'},
						{datas:this.one_major_id,text:'当前选择学科门类为空'},
						{datas:this.two_major_id,text:'当前选择一级学科为空'},
						{datas:this.three_major_id,text:'当前选择专业名称为空'},
						{datas:this.one_subject,text:'当前选择第一门科目为空'},
						{datas:this.two_subject,text:'当前选择第二门科目为空'},
						{datas:this.three_subject,text:'当前选择第三门科目为空'},
						{datas:this.four_subject,text:'当前选择第四门科目为空'},
					]
					let isOver = true
					let that = this
					// 验证是否填写相应信息
					tipArr.map(item => {
						if(item.datas==''){
							this.$u.toast(item.text)
							isOver = false
						    throw Error()
						}
					})
					// 验证表单是否填写完整
					if(isOver==true){
						if(this.$u.utils.networkType()==true){
							submitInfo({...params}).then(res=>{
								console.log(res)
								if(res.data.code==-1){
									if(res.data.data=='请勿重复提交'){
										uni.navigateTo({
											url:'/pages/assessment/assessmentStepTwo'
										})
									}else{
										this.$u.toast(res.data.data)
									}
								}else{
									this.$u.toast(res.data.data)
									setTimeout((item)=>{
										uni.navigateTo({
											url:'/pages/assessment/assessmentStepTwo'
										})
									},1000)
								}
							}).catch(err=>{
								console.log(err)
							})
						}else{
							this.$u.toast('当前网络状况不佳~')
						}
					}
				}
			}
		}
	}
</script>

<style lang="scss">
	.body {
		min-height: 100vh;
		background-color: #f7f6fb;
		padding: 30rpx;
		padding-bottom: 0;
		.uni-select__selector{
			overflow-y: scroll !important;
			height: 300rpx !important;
		}
		.u-steps-item__wrapper {
			width: 0 !important;
		}
		.uni-select__selector-item{
			text{
				width: 100%;
				text-overflow: ellipsis;
				white-space: nowrap;
				overflow: hidden;
			}
		}
		.u-steps-item__wrapper__dot {
			width: 20rpx !important;
			height: 20rpx !important;
		}

		.textList {
			display: flex;
			width: 80%;
			font-size: 26rpx;
			justify-content: space-between;
			margin: 0 auto;
			color: #606266;

			.text {
				line-height: 42rpx;
				text-align: center;
			}

			.checked {
				color: #ffd656;
			}
		}

		.content {
			border-radius: 4rpx;
			background-color: #fff;
			margin: 20rpx 0;
			padding: 30rpx;

			.list {
				.cell {
					display: flex;
					justify-content: space-between;
					align-items: center;
					font-size: 28rpx;
					line-height: 58rpx;
					margin-bottom: 10rpx;
					color: #545454;

					.label {
						width: 30%;
					}

					.check {
						width: 70%;
						background-image: url(../../static/assessment/check_icon.png);
						background-repeat: no-repeat;
						background-position: 95% center;
						background-size: 20rpx;

						.uni-select {
							font-size: 28rpx !important;
							border-radius: 0 !important;
							line-height: 68rpx;
						}
					}
				}

				.disable {
					.uni-select__input-text {
						color: #b2b2b2 !important;
					}
				}
			}

			.title {
				margin: 30rpx 0;
				color: #ffd656;
				font-size: 30rpx;
			}

			.testMoudel {
				.part {
					display: flex;
					justify-content: space-between;

					.cell {
						width: 40%;

						.subNum {
							font-size: 34rpx;
						}

						.check {
							background-image: url(../../static/assessment/sub_icon.png);
							background-repeat: no-repeat;
							background-position: 90% center;
							background-size: 20rpx;
							.uni-select {
								text-align: left;
								font-size: 28rpx !important;
								border: none !important;
								line-height: 88rpx;
								padding-left: 0 !important;
							}
						}

						.defult {
							.uni-select__input-text {
								color: #b2b2b2;
							}
						}
					}
				}

				.part:first-child {
					border-bottom: 1rpx solid #eee;
					margin-bottom: 30rpx;
				}
			}

			.beginButton {
				background-color: #ffd656;
				color: #fff;
				font-size: 38rpx;
				width: 300rpx;
				margin: 50rpx auto;
				border-radius: 50rpx;
				text-align: center;
				padding: 0;
				line-height: 78rpx;
			}
		}
	}
</style>
